export const NAME_GENERATOR = {
  getFantasyName: function () {
    var d20 = Math.floor(Math.random() * 20) + 1,
      name = '',
      rngFirstName,
      rngSecondName;

    if (d20 < 3) {
      // 10%
      name = names[0][Math.floor(Math.random() * names[0].length)];
    } else if (d20 < 12) {
      // 45%
      name = names[1][Math.floor(Math.random() * names[1].length)];
    } else if (d20 < 17) {
      // 25%
      name = names[2][Math.floor(Math.random() * names[2].length)];
    } else if (d20 === 17) {
      // 5%
      name = names[3][Math.floor(Math.random() * names[3].length)];
    } else if (d20 === 18) {
      // 5%
      name =
        names[0][Math.floor(Math.random() * names[0].length)] +
        ' ' +
        names[1][Math.floor(Math.random() * names[1].length)];
    } else if (d20 === 19) {
      // 5%
      name =
        names[1][Math.floor(Math.random() * names[1].length)] +
        ' ' +
        names[0][Math.floor(Math.random() * names[0].length)];
    } else {
      // 5%
      rngFirstName = names[Math.floor(Math.random() * names.length)];
      rngSecondName = names[Math.floor(Math.random() * names.length)];

      name =
        rngFirstName[Math.floor(Math.random() * rngFirstName.length)] +
        ' ' +
        rngSecondName[Math.floor(Math.random() * rngSecondName.length)];
    }

    return name;
  },
};
